import React, { Component } from 'react';
import { Button, Modal, } from 'react-bootstrap'
import { Carousel } from 'react-bootstrap';

class ProductInfoModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            show: false,

        }
    }
    handleClose = () => {
        this.props.handleClose();
    }
    render() {
        let { product } = this.props
        return (
            <>
                <Modal show={this.props.show} onHide={this.handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>{this.props.title}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="d-flex justify-content-between px-2">
                            <span className="float-start">{product.title}</span>
                            <span className="float-end">{product.currency} {product.price}</span>
                        </div>
                        <hr />
                        {product.images && product.images.length > 0 &&
                            <div>
                                <Carousel>
                                    {product.images.map((image, index) => {
                                        return (
                                            <Carousel.Item>
                                                <img
                                                    className="d-block w-100"
                                                    src={`${this.props.origin}${image.file.url}`}
                                                    alt={image.file_name}
                                                />
                                            </Carousel.Item>
                                        )
                                    })}

                                </Carousel>
                                <hr />
                            </div>
                        }

                        <div style={{ height: "100px" }} className="overflow-auto mt-1 px-2" >
                            <div>{product.description}</div>
                            {product.attachments && product.attachments.length > 0 && <div>
                                {product.attachments.map((f, index) => {
                                    return (
                                        <span className="mx-1">
                                            <a className="text-decoration-none" href={`${this.props.origin}${f.file.url}`} download >{f.file_name}</a>
                                        </span>
                                    )
                                })}
                            </div>
                            }

                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" style={{ background: this.props.buttonColor }} onClick={this.handleClose}>
                            {this.props.btnText}
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>
        )
    }
}

export default ProductInfoModal;
